import { Component } from '@angular/core';
import {AppStoreService} from "./service/res/app-store/app-store.service";
import {Router} from "@angular/router";

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'angular-app';

  result =0;


  constructor(
    private appStore: AppStoreService,
    private router: Router
  ) {
  }

  public onClickSumButton(){
    const result =  this.appStore.sum(5,5)
    this.result = result;
  }

  /**
   * Элементы меню
   */
  getMenuItems() {
    return this.appStore.menuItems;
  }

  /**
   * Логотип
   */
  getLogoUrl() {
    return this.appStore.logoUrl;
  }

  /**
   * Текс с кнопки в Header
   */
  getSubscribeText() {
    return this.appStore.subscribeText;
  }

  /**
   * Абзац с текстом в Footer
   */
  getFooterDesc() {
    return this.appStore.footerDescr;
  }

  /**
   * Меню футера
   */
  getFooterItems() {
    return this.appStore.footerItems;
  }

  /**
   * Смена языка
   */
  getLanguageItems() {
    return this.appStore.languageItems;
  }

  /**
   *Переход между страницами
   */
  public clickOnMenu({detail}) {
    console.log('click on menu', detail);
    if (detail.id == 'page1') {
      this.router.navigate(['first-page']);
    }else {
      this.router.navigate(['second-page']);
    }
  }

  /**
   * Клик по лого
   * @param detail
   */
  public clickOnLogo({detail}) {
    console.log('clickOnLogo', detail);
  }

  /**
   * Клик по кнопке Subscribe в Header
   * @param detail
   */
  public clickOnSubscribe({detail}) {
    console.log('clickOnSubscribe', detail);
  }

  /**
   * Клик по ссылкам в меню Footer
   * @param detail
   */
  public clickOnFooterLinks({detail}) {
    console.log('clickOnFooterLinks', detail);
  }

  /**
   * Клик по смене языка
   * @param detail
   */
  public clickOnLanguage({detail}) {
    console.log('clickOnLanguage', detail);
  }

}
